.MenuBtn_MenuBtn__container__1Plpx,
.MenuBtn_MenuBtn__container__active__2jGCM {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  cursor: pointer;
  outline: none;
  border-radius: 50%;
  transition: var(--primary-transition);
}
.MenuBtn_MenuBtn__3bgyM {
  width: 30px;
  height: 3px;
  background-color: var(--header-title-color);
  border-radius: 1.5px;
  transition: var(--primary-transition);
}
.MenuBtn_MenuBtn__3bgyM::before,
.MenuBtn_MenuBtn__3bgyM::after {
  content: '';
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: var(--header-title-color);
  border-radius: 1.5px;
  transition: var(--primary-transition);
}
.MenuBtn_MenuBtn__3bgyM::before {
  transform: translateY(-10px);
}
.MenuBtn_MenuBtn__3bgyM::after {
  transform: translateY(10px);
}

/* onClick */

.MenuBtn_MenuBtn__container__active__2jGCM .MenuBtn_MenuBtn__3bgyM {
  transform: translateX(-50px);
  background: transparent;
}
.MenuBtn_MenuBtn__container__active__2jGCM .MenuBtn_MenuBtn__3bgyM::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.MenuBtn_MenuBtn__container__active__2jGCM .MenuBtn_MenuBtn__3bgyM::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

