/* Links */
.Projects_link__container___Kr7F {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 30%;
  opacity: 0;
  transition: 0.2s linear;
}
.Projects_link__2aJls {
  position: relative;
  text-decoration: none;
  color: var(--header-title-color);
  font: var(--primary-font);
  transition: 0.2s linear;
}
.Projects_link__2aJls:not(:last-child) {
  margin-right: 20%;
}
.Projects_link__2aJls::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 1px;
  transform: scaleX(0);
  background-color: var(--header-title-color);
  margin-top: 5px;
  transition: 0.2s linear;
}
/* On hover link */
.Projects_list__item__2nkZj:hover .Projects_link__container___Kr7F,
.Projects_list__item__2nkZj:focus .Projects_link__container___Kr7F {
  background-color: var(--dark-overlay);
  border-radius: 20px;
  opacity: 1;
  transition: 0.2s linear;
}
.Projects_link__2aJls:hover,
.Projects_link__2aJls:focus {
  color: var(--white);
  transition: 0.2s linear;
}
.Projects_link__2aJls:hover::after,
.Projects_link__2aJls:focus::after {
  transform: scaleX(0.7);
  transition: 0.2s linear;
}

@media screen and (min-width: 1280px) {
  .Projects_list__2pwum {
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 20px;
  }
}

